.card {
  width: 300px;
  height: 350px;
  background-color: #0072bc;
  text-align: center;
  padding: 15px;
  margin: 10px auto;
  position: relative;
  overflow: hidden;
  border-radius: 10px;
  box-shadow: 0 0 11px rgba(33, 33, 33, 0.2);
  color: white;
}
.card::before {
  content: "";
  position: absolute;
  top: -250px;
  left: -50px;
  width: 400px;
  height: 400px;
  background-color: black;
  border-radius: 50%;
}
.card .image {
  height: 187.6px;
  width: 70%;
  position: relative;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  margin: 0, auto;
  border-radius: 50%;
  border: 5px solid white;
  overflow: hidden;
}
.card .image img {
  /* height: 100%; */
  width: 100%;
  object-fit: cover;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  -webkit-filter: blur(0) brightness(1);
          filter: blur(0) brightness(1);
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.card .image .links {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  font-size: 2.5rem;
  color: black;
  opacity: 0;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  text-decoration: none;
}
.card .image:hover img {
  -webkit-filter: blur(1px) brightness(3);
          filter: blur(1px) brightness(3);
}
.card .image:hover .links {
  opacity: 1;
}

.card .image .links a {
  color: black;
}

.info {
  margin-top: 10px;
}

.info h2 {
  font-size: 1.5em;
}

.MemberDetails {
  padding: 0 150px;
  text-align: center;
}
